import React from "react";

export type DatePickerPopoverContextType = {
	closeCalendarPopover: () => void,
};

export const DatePickerPopoverContext = React.createContext<DatePickerPopoverContextType | null>(null);

export const useDatePickerPopoverContext = (): DatePickerPopoverContextType => {
	const context = React.useContext(DatePickerPopoverContext);
	if (!context) {
		throw new Error("usePopoverContext must be used with PopoverContext");
	}
	return context;
};

export const DatePickerPopoverContextProvider = DatePickerPopoverContext.Provider;
